.link-callback,
.link-region {
  position: relative;

  font-family: @myriad;
  font-size: 18px;
  font-weight: 400;
  color: #161616;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.link-callback {
  padding-right: 39px;
  &::before {
    top: @a;
    bottom: -12px;
    width: 100%;
    height: 1px;
    background-color: #9BB1FF;
  }
  &::after {
    top: 2px;
    width: 10px;
    height: 17px;
    right: 0px;

    background-image: url('../img/svg/arrow-b.svg');
  }
}

.link-region {
  text-decoration-line: underline;
  &::before {
    top: -8px;
    left: -55px;
    width: 45px;
    height: 45px;
    background-image: url('../img/svg/region-point-b.svg');
  }
}

.bg {
  .link-callback,
  .link-region {
    font-family: @roboto;
    font-weight: 700;
    color: #fff;
  }
  .link-callback {
    padding-right: 29px;
    &::before {
      bottom: -8px;
      background-color: #fff;
    }
    &::after {
      background-image: url('../img/svg/arrow-w.svg');
    }
  }
}

.subtitle-pink {
  margin-bottom: 12px;
  font-size: 18px;
  line-height: 22px;

  color: #C63067;
}

